
import './preload.scss';
import './scroll';
import apiService from './apiService';
import updateHitsMarkup from './update-hits';
import * as PNotify from '@pnotify/core';
import '@pnotify/core/dist/BrightTheme.css';
import '@pnotify/core/dist/PNotify.css';


const listRef = document.querySelector('.gallery')

const tittleRef = document.querySelector('.scrollTittle');
const tittleSignRef = document.querySelector('.sign');


const debounce = require('lodash.debounce');
const serchRef = document.querySelector('.search-form')

const rootTarget = document.querySelector('.js-rootTarget')   
let inputValue = ''

const closeModalBtnRef = document.querySelector(
  'button[data-action="close-lightbox"]',
);
const openModalRef = document.querySelector('.js-lightbox');
const overlayRef = document.querySelector('.lightbox__overlay');
const imgModal = document.querySelector('.lightbox__image');

const lightbox小ontentRef = document.querySelector('.lightbox__content');

closeModalBtnRef.addEventListener('click', clsModal);
listRef.addEventListener('click', opnModalFunc);



const preLoad = document.querySelector('.load');

function opnModalFunc(event) {
  event.preventDefault();


  if (event.target.nodeName === 'IMG') {
    preLoad.classList.remove('done')
    openModalRef.classList.add('is-open');
       lightbox小ontentRef.style.opacity = 0 
     imgModal.src = event.target.dataset.source;
    
  } 
  
}
imgModal.onload = function () {
  
     
  setTimeout(() => {
    preLoad.classList.add('done')
    lightbox小ontentRef.style.opacity = 1 
  }, 2000)
  }

function clsModal(event) {
  if (event.target === event.currentTarget) {
    openModalRef.classList.remove('is-open');
    imgModal.removeAttribute('src');
  }
}

 
serchRef.addEventListener('input', debounce(event => {
    
     inputValue = event.target.value
  if (inputValue) {
    tittleRef.classList.add('hidden')
    tittleSignRef.classList.add('hidden')
  PNotify.success({
     text: `Search : ${inputValue}`,
    delay: 1000,
    addClass: 'PNotify',
    addModalClass: 'PNotify',
   addModelessClass: 'PNotify',
    styling: 'scss'
        });
        apiService.query = inputValue
        apiService.resetPage();
        apiService.fetchHits()
            .then(hits => {
                console.log(hits);
             updateHitsMarkup(hits);
             io.observe(rootTarget) 
                
})
    } 
        
  listRef.innerHTML = "";
   
    
  }, 1000))



const options = {
    rootMargin: '200px'
}
const io = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
        if (entry.isIntersecting && inputValue) {
 PNotify.success({
     text: `Load more ${inputValue} !`,
     delay: 1000,
        });
            apiService.fetchHits()
                .then(hits => {
                    updateHitsMarkup(hits)
                })
        }
    })},options);
 

    

    
   


      
